import React from 'react'

const CedulaCandidato = ({ candidato, setElegido, elegido }) => {
  return (
    <div className={elegido === candidato.id ? "card active" : "card"} onClick={() => {
      setElegido(candidato.id);
    }}>
      <figure className="card__avatar">
        <img src={candidato.partido_img_candidato} alt="" />
      </figure>
      <div className="card__info">
        <p className="card__name">Nombre del candidato</p>
        <p className="card__partido">{candidato.partido_nombre}</p>
      </div>
      <figure className="card__logo">
        <img src={candidato.partido_img_partido} alt="" />
      </figure>
    </div>
  )
}

export default CedulaCandidato
