import { URL_BACKEND } from "../environments/environments";

export const postRegistro = async ({ enpoint, body }) => {
  const peticion = await fetch(`${URL_BACKEND}/registro`, {
    method: "POST",
    body: JSON.stringify(body),
    headers: {
      "Content-type": "application/json"
    }
  });
  const data = await peticion.json();
  return data;
}


export const getValidar = async id => {
  const peticion = await fetch(`${URL_BACKEND}/votante?id=${id}`);
  const data = await peticion.json();
  return data;
}

export const postVotar = async (hash, partido) => {
  const peticion = await fetch(`${URL_BACKEND}/voto`, {
    method: "POST",
    body: JSON.stringify({
      hash, partido
    }),
    headers: {
      "Content-type": "application/json"
    }
  });
  const data = await peticion.json();
  return data;
}

export const getCandidatos = async _ => {
  const peticion = await fetch(`${URL_BACKEND}/partido`);
  const data = await peticion.json();
  return data;
}

export const getResultados = async _ => {
  const peticion = await fetch(`${URL_BACKEND}/resultados`);
  const data = await peticion.json();
  return data;
}