import React, { useState } from 'react'
import "./../../styles/formulario.scss"
import { useForm } from "react-hook-form";
import Swal from "sweetalert2";
import { postRegistro } from '../../services/votacionesService';
import { validarDniRequired, validarDniMaxLength, validarDniPattern, changingInput, deleteKey } from '../../utils/utils';
import Loading from '../../components/Loading';
const FormularioScreen = ({ history }) => {
  const { register, handleSubmit, errors } = useForm();
  const [cargando, setCargando] = useState(false);
  console.log(errors);
  const onSubmit = (data) => {

    console.log(data);
    const { dni1, dni2, dni3, dni4, dni5, dni6, dni7, dni8, dniv, email } = data;
    Swal.fire({
      title: "¿Seguro de ingresar al voto electrónico?",
      text: "Se enviará un correo electrónico al email que ha indicado con un enlace para proceder al voto",
      icon: "warning",
      showCancelButton: true
    }).then(({ isConfirmed }) => {
      if (isConfirmed) {
        setCargando(true);
        postRegistro({
          body: {
            votante_dni: dni1.trim() + dni2.trim() + dni3.trim() + dni4.trim() + dni5.trim() + dni6.trim() + dni7.trim() + dni8.trim(),
            votante_email: email,
            votante_verificacion: dniv
          }
        }).then(data => {

          if (data.success) {
            Swal.fire({
              title: "Registrado!",
              icon: "success",
              confirmButtonText: "De acuerdo",
              text: `En horabuena ${data.votante_nombre} ${data.votante_apellido}!, se ha mandado un link a su correo electrónico para votar.`
            }).then(() => {
              history.replace("/");
            });
          } else {
            Swal.fire({
              title: "Ups!",
              icon: "error",
              confirmButtonText: "De acuerdo",
              text: data.message
            }).then(() => {
              history.replace("/");
            });
          }
        });
      }
    })

  }
  return (
    <main className="formulario">
      {
        cargando && <Loading />
      }
      <form className="form" onSubmit={handleSubmit(onSubmit)}>
        <label htmlFor="">Ingrese su número de DNI:</label>
        <div className="errores">
          {validarDniRequired(errors)}
          {validarDniMaxLength(errors)}
          {validarDniPattern(errors)}
        </div>
        <div className="form__group">
          <input type="text"
            ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dni1" onChange={e => { changingInput("dni1", "dni2", "") }}
          />
          <input type="text"
            ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dni2" onChange={e => { changingInput("dni2", "dni3", "dni1") }}
            onKeyUp={e => { deleteKey("dni2", e.key, "dni1") }}
          />
          <input type="text"
            ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dni3" onChange={e => { changingInput("dni3", "dni4", "dni2") }}
            onKeyUp={e => { deleteKey("dni3", e.key, "dni2") }}
          />
          <input type="text"
            ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dni4" onChange={e => { changingInput("dni4", "dni5", "dni3") }}
            onKeyUp={e => { deleteKey("dni4", e.key, "dni3") }}
          />
          <input type="text"
            ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dni5" onChange={e => { changingInput("dni5", "dni6", "dni4") }}
            onKeyUp={e => { deleteKey("dni5", e.key, "dni4") }}
          />
          <input type="text"
            ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dni6" onChange={e => { changingInput("dni6", "dni7", "dni5") }}
            onKeyUp={e => { deleteKey("dni6", e.key, "dni5") }}
          />
          <input type="text"
            ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dni7" onChange={e => { changingInput("dni7", "dni8", "dni6") }}
            onKeyUp={e => { deleteKey("dni7", e.key, "dni6") }}
          />
          <input type="text"
            ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dni8" onChange={e => { changingInput("dni8", "dniv", "dni7") }}
            onKeyUp={e => { deleteKey("dni8", e.key, "dni7") }}
          />
        </div>
        <label htmlFor="">Número de verificación de DNI:</label>
        <div className="errores">
          {errors?.dniv?.type === "pattern" && <small className="error">*** El número de verificación del DNI, debe ser un caracter númerico</small>}
          {errors?.dniv?.type === "required" && <small className="error">*** El campo de verificaicón del DNI es obligatorio</small>}

        </div>
        <div className="form__group">
          <input type="text" ref={register({ required: true, maxLength: 1, pattern: /^[0-9]/i })}
            name="dniv" onChange={() => { changingInput("dniv", "email", "dni8") }}
            onKeyUp={e => { deleteKey("") }} />
        </div>
        <label htmlFor="">E-mail:</label>
        <div className="errores">
          {errors?.email?.type === "pattern" && <small className="error">*** El formato del email no es correcto, debe tener un @ y un .</small>}
          {errors?.email?.type === "required" && <small className="error">*** El campo del email es obligatorio</small>}

        </div>
        <input type="email" ref={register({
          required: true, pattern: /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$/i
        })} name="email" />
        <div className="form__button">
          <button type="submit">
            Votar
        </button>
        </div>
      </form>
    </main>
  )
}

export default FormularioScreen
