import React from 'react'

const IndexScreen = ({ history }) => {

  const handleSubmit = e => {
    e.preventDefault();
    history.push("/formulario");
  }

  return (
    <main className="main">
      <section className="main__menu">
        <div className="main__circle" onClick={() => {
          history.push("/estadisticas");
        }}>
          <p className="main__icono">
            <i className="fas fa-chart-bar"></i>
          </p>
          <p className="main__title">Estadísticas</p>
        </div>
        <div className="main__circle">
          <p className="main__icono">
            <i className="fas fa-list    "></i>
          </p>
          <p className="main__title">Lista de Cantidatos</p>
        </div>
        <div className="main__circle">
          <p className="main__icono">
            <i className="fas fa-home"></i>
          </p>
          <p className="main__title">Otra Opción</p>
        </div>
      </section>
      <form className="main__form" onSubmit={handleSubmit}>
        <button type="submit">
          Quiero votar
      </button>
      </form>
    </main>
  )
}

export default IndexScreen
