import React from 'react'
import CedulaCandidato from './CedulaCandidato'

const CedulaLista = ({ candidatos = [], setElegido, elegido }) => {
  return (
    <section className="lista">
      {
        candidatos.map(c => (
          <CedulaCandidato key={c.id} candidato={c}
            setElegido={setElegido} elegido={elegido} />
        ))
      }

    </section>

  )
}

export default CedulaLista
