import React, { useEffect, useState } from 'react'
import { io } from "socket.io-client";
import { Bar } from "react-chartjs-2";
import "./../../styles/estadisticas.scss";
import { URL_BACKEND } from '../../environments/environments';
import Loading from '../../components/Loading';
import { getCandidatos, getResultados } from '../../services/votacionesService';

const EstadisticasScreen = () => {

  const [data, setData] = useState([]);
  const [cargando, setCargando] = useState(true);
  const [candidatos, setCandidatos] = useState([]);
  useEffect(() => {
    Promise.all([getCandidatos(), getResultados()]
    ).then(data => {
      const [candidatos = [], resultados = []] = data;
      if (candidatos.success && resultados.success) {
        console.log(resultados);
        const labels = candidatos.content.map(partido => partido.partido_nombre);
        const datasets = [
          {
            label: 'Cantidad de votos por partido',
            backgroundColor: 'rgba(255,99,132,0.2)',
            borderColor: 'rgba(255,99,132,1)',
            borderWidth: 1,
            hoverBackgroundColor: 'rgba(255,99,132,0.4)',
            hoverBorderColor: 'rgba(255,99,132,1)',
            data: candidatos.content.map(candidato => {
              const resultado_con_voto = resultados.content.find(resultado => candidato.id === resultado.id)
              if (resultado_con_voto) {
                return resultado_con_voto.voto
              } else {
                return 0
              }
            }),
          }
        ];
        setData({ labels, datasets });
        setCargando(false);
        setCandidatos(candidatos.content);
      }
    })

  }, [])

  // useEffect(() => {
  //   const socket = io(URL_BACKEND);
  //   socket.on("votos", (data = []) => {
  //     const labels = data.map(partido => partido.partido_nombre);
  //     const datasets = [
  //       {
  //         label: 'Cantidad de votos por partido',
  //         backgroundColor: 'rgba(255,99,132,0.2)',
  //         borderColor: 'rgba(255,99,132,1)',
  //         borderWidth: 1,
  //         hoverBackgroundColor: 'rgba(255,99,132,0.4)',
  //         hoverBorderColor: 'rgba(255,99,132,1)',
  //         data: data.map(resultado => {

  //           const resultado_con_voto = candidatos.find(candidato => candidato.id === resultado.id)

  //           if (resultado_con_voto) {
  //             return resultado.votos
  //           } else {
  //             return 0
  //           }
  //         })
  //       }
  //     ];
  //     setData({ labels, datasets });
  //     setCargando(false);
  //   })
  //   return () => {
  //     console.log("desconectando socket");
  //     socket.disconnect();
  //   }
  // }, [])

  return (
    <main className="estadisticas">
      {
        cargando ? <Loading /> :
          <>
            <section className="estadisticas__title">
              <h1>RESULTADOS EN TIEMPO REAL</h1>
            </section>
            <section className="estadisticas__indicaciones">
              <p>Cada vez que un nuevo voto sea registrado, usted verá dichos cambios en tiempo real.</p>
            </section>
            <section className="estadisticas__chart">

              <Bar data={data}
                options={{
                  maintainAspectRatio: false
                }}
              />

            </section>
          </>
      }

    </main>
  )
}

export default EstadisticasScreen
