import React from 'react'
import { BrowserRouter, Switch, Route, Link, HashRouter } from "react-router-dom";
import Footer from '../components/Footer';
import Header from '../components/Header';
import CedulaScreen from '../screens/cedula/CedulaScreen';
import EstadisticasScreen from '../screens/estadisticas/EstadisticasScreen';
import FormularioScreen from '../screens/formulario/FormularioScreen';
import IndexScreen from '../screens/index/IndexScreen';

const MainRouter = () => {
  return (
    <HashRouter>
      <Header />
      <Switch>
        <Route path="/formulario" component={FormularioScreen} />
        <Route path="/estadisticas" component={EstadisticasScreen} />
        <Route path="/cedula" component={CedulaScreen} />
        <Route path="/" component={IndexScreen} />
      </Switch>
      <Footer />
    </HashRouter>
  )
}

export default MainRouter
