import React, { useEffect, useState } from 'react'
import Swal from 'sweetalert2';
import Loading from '../../components/Loading';
import { getValidar, postVotar } from '../../services/votacionesService';
import "./../../styles/cedula.scss";
import CedulaLista from './components/CedulaLista';
import moment, { months } from "moment";
let timerInterval;
const CedulaScreen = ({ history }) => {
  console.log(history);
  const [cargando, setCargando] = useState(true);
  const [candidatos, setCandidatos] = useState([]);
  const [countDown, setCountDown] = useState();
  const [elegido, setElegido] = useState(null)
  const parametros = new URLSearchParams(window.location.search);
  const id = parametros.get("id");


  const handleSubmit = e => {
    e.preventDefault();

    Swal.fire({
      title: "¿Seguro de emitir su voto?",
      text: "Los cambios serán irreversible y sólo puede votar una vez",
      icon: "warning",
      showCancelButton: true,
    }).then(({ isConfirmed }) => {
      if (isConfirmed) {
        setCargando(true);
        postVotar(id, elegido).then((data) => {
          if (data.success) {
            Swal.fire({
              titleText: "Gracias ciudadano!, tu voto ha sido registrado con éxito",
              title: "Bien hecho!",
              icon: "success",
              timer: 5000,
              allowOutsideClick: false,
              timerProgressBar: true,
              didOpen: () => {
                Swal.showLoading()
                timerInterval = setInterval(() => {
                  const content = Swal.getContent()
                  if (content) {
                    const b = content.querySelector('b')
                    if (b) {
                      b.textContent = Swal.getTimerLeft()
                    }
                  }
                }, 100)
              },
              willClose: () => {
                clearInterval(timerInterval)
              }
            }).then(() => {
              setCargando(false);
              history.replace("/");
            })
          }
        })
      }
    })
  }

  useEffect(() => {
    let interval;
    if (id) {
      getValidar(id).then(data => {
        if (data.success) {
          let exp = moment(data.content.expiracion)
          setCargando(false);
          setCandidatos(data.content.partidos);
          interval = setInterval(() => {
            let ahora = moment();

            // let segundos = moment.duration(exp.diff(ahora)).asSeconds().toFixed(0);
            // let minutos = segundos / 60;
            // let segundosQuedan = segundos % 60;

            setCountDown(moment.duration(exp.diff(ahora)).asSeconds().toFixed(0));
          }, 1000);
        } else {
          Swal.fire({
            title: "Ups!",
            icon: "error",
            text: "Un error ha ocurrido, es posible que tu sesión haya caducado, intenta registrarte nuevamente",
            backdrop: `rgba(40,6,21,0.8)`,
            timer: 5000,
            allowOutsideClick: false,
            timerProgressBar: true,
            didOpen: () => {
              Swal.showLoading()
              timerInterval = setInterval(() => {
                const content = Swal.getContent()
                if (content) {
                  const b = content.querySelector('b')
                  if (b) {
                    b.textContent = Swal.getTimerLeft()
                  }
                }
              }, 100)
            },
            willClose: () => {
              clearInterval(timerInterval)
            }
          }).then(_ => {
            history.replace("/");
          })
        }
      })
    } else {
      setCargando(false);
      Swal.fire({
        icon: "error",
        title: 'Ups! tenemos un error con el link al que ingresaste. <br/>Serás redireccionado a la página principal',
        backdrop: `rgba(40,6,21,0.8)`,
        timer: 5000,
        allowOutsideClick: false,
        timerProgressBar: true,
        didOpen: () => {
          Swal.showLoading()
          timerInterval = setInterval(() => {
            const content = Swal.getContent()
            if (content) {
              const b = content.querySelector('b')
              if (b) {
                b.textContent = Swal.getTimerLeft()
              }
            }
          }, 100)
        },
        willClose: () => {
          clearInterval(timerInterval)
        }
      }).then(_ => {
        history.replace("/");
      })
    }
    return () => {
      clearInterval(interval);
    }

  }, [id]);

  return (
    <>
      {cargando && <Loading />}
      <main className="cedula">
        <section className="cedula__title">
          <h1>LISTA DE CANDIDATOS</h1>
          <p>Le quedan {countDown} segundos</p>
        </section>
        <section className="cedula__indicaciones">
          <p>Seleccione el candidato de su preferencia y luego presione “votar”</p>
        </section>
        <CedulaLista candidatos={candidatos}
          setElegido={setElegido} elegido={elegido} />
      </main>
      <form className="cedula__form" onSubmit={handleSubmit}>
        <button type="submit" disabled={!elegido}>
          Votar
        </button>
      </form>
    </>
  )
}

export default CedulaScreen
